import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import style from './header-bottom.module.css'

const HeaderBottom = () => {
    const navigate = useNavigate();

    return (
        <div className={style.headerBottom}>
            <nav>
                <ul className={style.topMenu}>
                    <li>
                        <NavLink></NavLink>
                        <ul className={style.subMenu}>
                            <li><span onClick={() => navigate("/catalog/2")}></span></li>
                            <li><span onClick={() => navigate("/catalog/3")}> </span></li>
                            <li><span onClick={() => navigate("/catalog/4")}>    </span></li>
                            <li><span onClick={() => navigate("/catalog/5")}> </span></li>
                        </ul>
                    </li>
                    <li><NavLink to='/'></NavLink></li>
                    <li><NavLink to='/delivery'>  </NavLink></li>
                    <li><NavLink to='/contacts'></NavLink></li>
                </ul>
            </nav>
        </div>
    );
};

export default HeaderBottom;